.btn-primary {
  width: 100%;
  padding-inline: 15px;
  background: #25ba7b;
  font-family: "Inter";
  font-weight: 700;
  line-height: 140%;
  color: #fff;
  text-decoration: none;
}
.btn-primary:hover {
  background-color: #00cd83;
}
.btn-primary:hover:active {
  background-color: #25ba7b;
}
.btn-primary.button-desktop-large {
  max-width: 301px;
  min-height: 65px;
  padding-block: 20px;
  border-radius: 15px;
  box-shadow: 0 8px 24px 0 rgba(37, 186, 123, 0.4);
  font-size: 18px;
}
@media (max-width: 1200px) {
  .btn-primary.button-tablet-medium {
    max-width: 222px;
    min-height: 50px;
    padding-block: 15px;
    border-radius: 12px;
