  .btn-primary.button-small {
    max-width: 184px;
    min-height: 37px;
    padding-block: 10px;
    border-radius: 10px;
    box-shadow: 0 4px 15px 0 rgba(37, 186, 123, 0.4);
    font-size: 12px;
  }
}

