}

.input-container {
  position: relative;
  max-width: 420px;
}

.input {
  display: block;
  width: 100%;
  height: 54px;
  border-radius: 12px;
  padding: 16px 48px 16px 16px;
  border: 1px solid #dee4ee;
  font-family: "Inter";
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: #3d475a;
  background-color: #fff;
}
@media (max-width: 1200px) {
  .input {
    height: 50px;
  }
}

.input::placeholder {
  font-family: "Inter";
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: #181c24;
}

.input.default {
  border: 1px solid #dee4ee;
}

.input.text {
  border: 1px solid #dee4ee;
}

.input.active {
  border: 1px solid #00cd83;
  box-shadow: 0 10px 30px 0 rgba(0, 79, 176, 0.1);
}

.input.error {
  border: 1px solid #ff5656;
}

.input-container img.input-icon {
  position: absolute;
  display: block;
  width: 16px;
  height: 16px;
  right: 19px;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
}

.error-box {
  display: flex;
  align-items: center;
  gap: 4px;
  margin-top: 4px;
}

.error-icon {
  display: block;
  width: 12px;
  height: 12px;
}

.error-text {
  font-family: "Inter";
  font-weight: 400;
  font-size: 12px;
  line-height: 140%;
  color: #ff5656;
}

.modal {
  visibility: hidden;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(24, 28, 36, 0.5);
}

.modal.active {
  visibility: visible;
}

.modal.active .modal__container {
  opacity: 1;
  transform: scale(1);
}

.modal__container {
  position: relative;
  opacity: 0;
  transform: scale(0);
  padding: 60px;
  max-width: 540px;
  border-radius: 30px;
  min-height: 664px;
  background-color: #f6f8fc;
  margin: 5% auto;
  transition: all 0.3s ease-in-out;
}

.modal__title {
  margin-bottom: 15px;
  font-family: "Inter";
  font-weight: 700;
  font-size: clamp(
    26px,
    calc(
      26px +
        (6 * ((100vw - 768px) / 432))
    ),
    32px
  );
  line-height: 130%;
  text-align: center;
  color: #181c24;
}

.modal__subtitle {
  max-width: 320px;
  margin-bottom: 20px;
  margin-inline: auto;
  font-family: "Inter";
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  text-align: center;
  color: #3d475a;
}

.modal__inputs-container {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 30px;
}

.modal__container .btn-close {
  position: absolute;
  right: 30px;
  top: 30px;
}

.modal__close-button:hover {
  cursor: pointer;
}

.modal__close-icon {
  width: 24px;
  height: 24px;
}

.modal__content {
  display: flex;
  flex-direction: column;
}

.modal__content .btn-primary {
  max-width: 100%;
  margin-bottom: 30px;
}

.modal__agree-text {
  font-family: "Inter";
  font-weight: 400;
  font-size: 12px;
  line-height: 140%;
  color: #818ea3;
}

.modal__agree-link {
  color: #4c53ff;
}

.show-button:hover {
  cursor: pointer;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown__button {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 300px;
  width: 100%;
  height: 54px;
  padding: 16px;
  border: 1px solid #dee4ee;
  border-radius: 12px;
  background: #fff;
  cursor: pointer;
}
.dropdown__button.show img {
  transform: rotate(180deg);
}
@media (max-width: 1200px) {
  .dropdown__button {
    height: 44px;
  }
}

.dropdown__button-text {
  font-family: "Inter";
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: #181c24;
}

.dropdown__button:hover {
  border: 1px solid #00cd83;
}

.dropdown__content {
  display: none;
  position: absolute;
  top: 58px;
  max-width: 300px;
  width: 100%;
  min-height: 150px;
  padding-block: 12px;
  border: 1px solid #dee4ee;
  border-radius: 12px;
  box-shadow: 0 10px 30px 0 rgba(0, 79, 176, 0.1);
  background: #fff;
  z-index: 1;
}

.dropdown__content.show {
  display: block;
}

.dropdown__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 16px;
  cursor: pointer;
}
.dropdown__item:hover {
  background-color: #f0f4fa;
}
.dropdown__item-icon {
  width: 12px;
  height: 12px;
  display: none;
  filter: brightness(0) saturate(100%) invert(77%) sepia(34%) saturate(6998%) hue-rotate(113deg) brightness(93%) contrast(101%);
}
.dropdown__item.selected .dropdown__item-icon {
  display: block;
}

.dropdown__item:hover {
  background-color: #f1f1f1;
}

