.dropdownSection__container {
  display: flex;
  flex-direction: column;
  gap: 40px;
  margin: 0 auto;
  max-width: 1000px;
}

.header {
  margin-bottom: 200px;
}
.header__wrapper {
  max-width: 1480px;
  margin: 0 auto;
}
@media (max-width: 1600px) {
  .header__wrapper {
    padding-inline: 15px;
  }
}
.header__top {
  min-height: 40px;
  background-color: #200069;
}
@media (max-width: 768px) {
  .header__top {
    min-height: 30px;
  }
}
.header .top-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 40px;
}
@media (max-width: 768px) {
  .header .top-wrapper {
    min-height: 30px;
  }
}
.header__top-nav {
  display: block;
}
@media (max-width: 768px) {
  .header__top-nav {
    display: none;
  }
}
.header__top-nav.mobile {
  display: none;
}
@media (max-width: 768px) {
  .header__top-nav.mobile {
    display: block;
    margin-bottom: 30px;
  }
}
.header__top-list {
  display: flex;
  gap: 15px;
}
.header__mobile {
  display: none;
  min-height: 56px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.25);
  padding: 11px;
}
@media (max-width: 768px) {
  .header__mobile {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__mobile-burger {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 34px;
  height: 34px;
  background-color: #200069;
}
.header .header__burger-close {
  display: none;
  filter: brightness(0) saturate(100%) invert(100%) sepia(0%) saturate(7444%) hue-rotate(87deg) brightness(97%) contrast(102%);
}
.header__mobile-phone {
  display: block;
  text-decoration: none;
}
.header__top-link {
  font-family: "Jost";
  font-weight: 500;
  font-size: 12px;
  line-height: 130%;
  text-transform: capitalize;
  color: #fff;
  text-decoration: none;
}
@media (max-width: 768px) {
  .header__top-link {
    font-size: 16px;
  }
}
.header__top-center {
  font-family: "Jost";
  font-weight: 500;
  font-size: 12px;
  line-height: 130%;
  color: #fff;
}
@media (max-width: 768px) {
  .header__top-center {
    display: none;
  }
}
.header__top-right {
  display: flex;
  align-items: center;
  gap: 7px;
}
@media (max-width: 768px) {
  .header__top-right {
    margin-left: auto;
  }
}
.header__top-number {
  font-family: "Jost";
  font-weight: 600;
  font-size: 12px;
  line-height: 100%;
  text-transform: capitalize;
  color: #fff;
}
.header__bottom {
  min-height: 97px;
  background-color: white;
}
@media (max-width: 768px) {
  .header__bottom {
    min-height: 56px;
    display: none;
  }
}
.header .bottom-wrapper {
  display: flex;
  align-items: center;
  min-height: 97px;
}
@media (max-width: 768px) {
  .header .bottom-wrapper {
    min-height: 56px;
  }
}
.header__logo {
  width: 141px;
  height: auto;
  margin-right: 50px;
  margin-right: clamp(
    20px,
    calc(
      20px +
        (30 * ((100vw - 1200px) / 200))
    ),
    50px
  );
}
.header__logo img {
  object-fit: contain;
}
.header__logo:hover {
  cursor: pointer;
}
.header__service-button {
  display: flex;
  align-items: center;
  width: 162px;
  height: 51px;
  padding: 15px;
  margin-right: 40px;
  margin-right: clamp(
    15px,
    calc(
      15px +
        (25 * ((100vw - 1200px) / 200))
    ),
    40px
  );
  border-radius: 6px;
  stroke-width: 2px;
  stroke: #200069;
  background-color: #200069;
  cursor: pointer;
}
.header__service-button img {
  display: block;
  width: 21px;
  height: 21px;
  transition: all 0.5s;
}
.header__service-button img.header__service-close {
  display: none;
  filter: brightness(0) saturate(100%) invert(100%) sepia(0%) saturate(3281%) hue-rotate(71deg) brightness(105%) contrast(101%);
}
.header__service-text {
  margin-left: 15px;
  font-family: "Jost";
  font-weight: 800;
  font-size: 18px;
  line-height: 100%;
  text-transform: uppercase;
  color: #fff;
}
@media (max-width: 768px) {
  .header__bottom-nav {
    display: none;
  }
}
.header__bottom-nav.mobile {
  display: none;
}
@media (max-width: 768px) {
  .header__bottom-nav.mobile {
    display: block;
    margin-bottom: 30px;
  }
}
.header__bottom-list {
  display: flex;
  gap: 25px;
  gap: clamp(
    15px,
    calc(
      15px +
        (10 * ((100vw - 1200px) / 200))
    ),
    25px
  );
}
@media (max-width: 1400px) {
  .header__bottom-list {
    margin-right: 15px;
    flex-wrap: wrap;
  }
}
.header__bottom-item:first-child {
  width: 102px;
  height: 23px;
  line-height: 23px;
  padding-inline: 9px;
  border-radius: 50px;
  background-color: #0c3;
}
.header__bottom-item:first-child a {
  color: #fff;
}
.header__bottom-link {
  font-family: "Jost";
  font-weight: 800;
  font-size: 13px;
  line-height: 130%;
  text-transform: uppercase;
  color: #200069;
  text-decoration: none;
}
@media (max-width: 768px) {
  .header__bottom-link {
    color: #fff;
  }
}
.header__bottom-right {
  display: flex;
  margin-left: auto;
  max-width: 354px;
  width: 100%;
  gap: 20px;
  flex-wrap: wrap;
}
@media (max-width: 1020px) {
  .header__bottom-right {
    flex-direction: column;
    gap: 10px;
    align-items: flex-end;
    max-width: 185px;
  }
}
.header__phone-box {
  display: flex;
  align-items: center;
  gap: 9px;
  max-width: 154px;
  width: 100%;
  cursor: pointer;
  text-decoration: none;
}
.header__phone-box img {
  display: block;
  width: 25px;
  height: 25px;
}
.header__phone-text {
  display: block;
  font-family: "Jost";
  font-weight: 800;
  font-size: 16px;
  line-height: 130%;
  text-transform: uppercase;
  color: #21006a;
}
.header__button {
  max-width: 180px;
  width: 100%;
  height: 51px;
  padding-block: 15px;
  border-radius: 50px;
  background-color: #0c3;
  font-family: "Jost";
  font-weight: 800;
  font-size: 16px;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  cursor: pointer;
}

.service {
  padding-top: 61px;
  opacity: 0;
  background-color: #200069;
  transition: all 0.5s ease;
}
@media (max-width: 1600px) {
  .service {
    padding-inline: 15px;
  }
}
@media (max-width: 768px) {
  .service {
    padding-top: 40px;
  }
}
.service__wrapper {
  max-width: 1102px;
  margin: 0 auto;
  padding-bottom: 60px;
  padding-left: 16px;
}
@media (max-width: 768px) {
  .service__wrapper {
    padding-left: 0;
  }
}
.service__grid {
  display: grid;
  grid-template-columns: 350px 400px 340px;
}
@media (max-width: 1200px) {
  .service__grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 768px) {
  .service__grid {
    grid-template-columns: 1fr;
  }
}
.service__column {
  border-right: 1px solid #350f8c;
  border-bottom: 1px solid #350f8c;
}
.service__column:nth-child(2) {
  padding-inline: 40px 60px;
}
@media (max-width: 1200px) {
  .service__column:nth-child(2) {
    padding-left: clamp(
    15px,
    calc(
      15px +
        (25 * ((100vw - 1000px) / 200))
    ),
    40px
  );
    padding-right: clamp(
    15px,
    calc(
      15px +
        (45 * ((100vw - 1000px) / 200))
    ),
    60px
  );
  }
}
@media (max-width: 768px) {
  .service__column:nth-child(2) {
    padding-left: 0;
  }
}
.service__column:nth-child(3) {
  padding-left: 40px;
  border-right: none;
}
@media (max-width: 1200px) {
  .service__column:nth-child(3) {
    padding-left: clamp(
    15px,
    calc(
      15px +
        (25 * ((100vw - 1000px) / 200))
    ),
    40px
  );
  }
}
@media (max-width: 768px) {
  .service__column:nth-child(3) {
    padding-left: 0;
  }
}
.service__title {
  margin-bottom: 24px;
  font-family: "Jost";
  font-weight: 800;
  font-size: 13px;
  line-height: 130%;
  text-transform: uppercase;
  color: #6443af;
}
.service__item {
  margin-bottom: 18px;
}
.service__link, .service__box-text {
  font-family: "Jost";
  font-weight: 800;
  font-size: 16px;
  line-height: 130%;
  text-transform: uppercase;
  color: #fff;
  text-decoration: none;
}
.service__box {
  display: flex;
  gap: 12px;
  align-items: center;
  padding-block: 18px;
  border-right: 1px solid #350f8c;
}
.service__box img {
  display: block;
  width: 24px;
  height: 24px;
}
.service__box-pic {
  padding: 20px;
}
.service__box:nth-child(5) {
  padding-inline: 40px 40px;
}
@media (max-width: 768px) {
  .service__box:nth-child(5) {
    padding-left: 0;
  }
}
.service__box:nth-child(6) {
  padding-left: 40px;
  border-right: none;
}
@media (max-width: 768px) {
  .service__box:nth-child(6) {
    padding-left: 0;
  }
}
.service__card {
  display: flex;
  align-items: center;
  min-height: 70px;
  margin-top: 25px;
}
.service__card:nth-child(7), .service__card:nth-child(8) {
  border-radius: 6px;
  background-color: #004ec8;
  border: 2px dashed #200069;
  margin-right: 11px;
}
@media (max-width: 768px) {
  .service__card:nth-child(7), .service__card:nth-child(8) {
    margin-right: 0;
  }
}
.service__card:nth-child(9) {
  border-radius: 6px;
  background-color: #00ab2b;
}
.service__card-text {
  font-family: "Jost";
  font-weight: 600;
  font-size: 14px;
  line-height: 120%;
  text-transform: uppercase;
  color: #fff;
}

.service.show {
  opacity: 1;
  transition: all 0.5s ease;
}

*,
*::after,
*::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  scroll-behavior: smooth;
  font-family: "Inter";
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: #3d475a;
  background-color: #fff;
  color: #181c24;
}

.container {
  max-width: 1480px;
  width: 100%;
  margin: 0 auto;
  padding-inline: clamp(
    15px,
    calc(
      15px +
        (105 * ((100vw - 768px) / 1152))
    ),
    120px
  );
}

ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

p {
  margin: 0;
  padding: 0;
}

input,
button {
  border: none;
  outline: none;
  background-color: transparent;
  appearance: none;
  padding: 0;
  margin: 0;
  font-family: inherit;
  color: inherit;
}

button:hover {
  cursor: pointer;
}